/* eslint-disable */
<template>
  <div>
    <Checkbox @on-change="queryByTime()">按照时间进⾏查询及统计</Checkbox>
    <Checkbox @on-change="queryByMovieName()">按照电影进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByDirectorName()">按照导演进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByActorName()">按照演员进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByMainActorName()">按照主演进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByType()">按照电影类别进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByCommenter()">按照评论者姓名进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByScore()">按照评分进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByCollaboration()">按照合作关系进行查询及统计</Checkbox>
    <Checkbox @on-change="queryBySimilarUser()">按照相似用户进行查询及统计</Checkbox>
    <Form :model="formItem" :label-width="80">
      <FormItem label="Database">
        <RadioGroup v-model="formItem.database">
          <Radio label="MySql">MySql</Radio>
          <Radio label="Neo4j">Neo4j</Radio>
          <Radio label="HBase">HBase</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem v-show="showActorName" label="演员姓名">
        <Input v-model="formItem.actorName" placeholder="输入演员姓名..."></Input>
      </FormItem>
      <FormItem v-show="showDirectorName" label="导演姓名">
        <Input v-model="formItem.directorName" placeholder="输入导演姓名..."></Input>
      </FormItem>
      <FormItem v-show="showMainActorName" label="主演姓名">
        <Input v-model="formItem.mainActorName" placeholder="输入主演姓名..."></Input>
      </FormItem>
      <FormItem v-show="showMovieName" label="电影名称">
        <Input v-model="formItem.movieName" placeholder="输入电影名称..."></Input>
      </FormItem>
      <FormItem v-show="showCommenter" label="评论">
        <Input v-model="formItem.commenterName" placeholder="输入评论者姓名..."></Input>
      </FormItem>
      <FormItem v-show="showScore" label="评分范围">
        <Slider v-model="formItem.scoreSlider" range :tip-format="tip" :max="50"></Slider>
      </FormItem>
      <FormItem v-show="showTime" label="时间范围">
        <Input v-model="formItem.time_from.day_of_month" placeholder="输入日期..."></Input>
        <Input v-model="formItem.time_from.day_fo_week" placeholder="输入星期..."></Input>
        <Input v-model="formItem.time_from.month" placeholder="输入月份..."></Input>
        <Input v-model="formItem.time_from.quarter" placeholder="输入季度..."></Input>
        <Input v-model="formItem.time_from.year" placeholder="输入年份..."></Input>
      </FormItem>
      <FormItem v-show="showCollaboration" label="合作关系">
        <Row>
          <Col span="11">
            合作类型：
            <Select v-model="formItem.collaborationType">
              <Option value="a-a">合作的演员</Option>
              <Option value="a-d">合作的演员和导演</Option>
              <Option value="d-d">合作的导演</Option>
            </Select>
          </Col>
          <Col span="2" style="text-align: center">-</Col>
          <Col span="11">
            合作次数：
            <Input v-model="formItem.collaborationTime" placeholder="输入合作次数..."></Input>
          </Col>
        </Row>
      </FormItem>
      <FormItem v-show="showType" label="类型">
        <Select v-model="formItem.type">
          <Option value="action">Action</Option>
          <Option value="fiction">Fiction</Option>
        </Select>
      </FormItem>
      <FormItem v-show="showType" label="版本">
        <Select v-model="formItem.version">
          <Option value="action">DVD</Option>
        </Select>
      </FormItem>
      <FormItem label="结果类型">
        <Select v-model="formItem.answer">
          <Option value="count">count</Option>
          <Option value="title">title</Option>
          <Option value="actor">actor</Option>
          <Option value="id">id</Option>
          <Option value="director">director</Option>
          <Option value="date">date</Option>
          <Option value="type">type</Option>
          <Option value="version">version</Option>
          <Option value="comment">comment</Option>
        </Select>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="submit">Submit</Button>
        <Button style="margin-left: 8px">Cancel</Button>
      </FormItem>
    </Form>
    <div v-show="showResult" style="background:#eee;padding: 20px">
      <Card :bordered="false">
        <p slot="title">query time||result number</p>
        <p>{{result.time}}||{{result.data.length}}</p>
      </Card>
    </div>
    <div class="overflow-auto" v-show="showResult">
      <b-table id="my-table" :items="items" :per-page="perPage" :current-page="currentPage" small></b-table>
      <b-pagination
        v-model="currentPage"
        :total-rows="rows"
        :per-page="perPage"
        aria-controls="my-table"
      ></b-pagination>
      <p class="mt-3">Current Page: {{ currentPage }}</p>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
import axios from "axios";
export default {
  data() {
    return {
      formItem: {
        idList: [],
        actorName: null,
        answer:null,
        directorName: null,
        mainActorName: null,
        commenterName: null,
        movieName: null,
        scoreSlider: [0, 0],
        type: null,
        version: null,
        time_from: {
          day_of_month: null,
          day_of_week: null,
          month: null,
          quarter: null,
          year: null
        },
        collaborationType: null,
        collaborationTime: null
      },
      database: "",
      showCollaboration: false,
      showTime: false,
      showName: false,
      showMovieName: false,
      showDirectorName: false,
      showActorName: false,
      showMainActorName: false,
      showType: false,
      showCommenter: false,
      showScore: false,
      showResult: false,
      showSimilarUser: false,
      result: {
        time: 0,
        data: []
      },
      info: null,
      perPage: 20,
      currentPage: 1,
      items: [
        { id: 1, first_name: "Fred", last_name: "Flintstone" },
        { id: 2, first_name: "Wilma", last_name: "Flintstone" },
        { id: 3, first_name: "Barney", last_name: "Rubble" },
        { id: 4, first_name: "Betty", last_name: "Rubble" },
        { id: 5, first_name: "Pebbles", last_name: "Flintstone" },
        { id: 6, first_name: "Bamm Bamm", last_name: "Rubble" },
        { id: 7, first_name: "The Great", last_name: "Gazzoo" },
        { id: 8, first_name: "Rockhead", last_name: "Slate" },
        { id: 9, first_name: "Pearl", last_name: "Slaghoople" }
      ]
    };
  },
  computed: {
    rows() {
      return this.items.length;
    }
  },
  methods: {
    tip(val) {
      return val / 10;
    },
    queryByTime() {
      this.showTime = !this.showTime;
      console.log(this.showTime);
    },
    queryByMovieName() {
      this.showMovieName = !this.showMovieName;
      console.log(this.showMovieName);
    },
    queryByDirectorName() {
      this.showDirectorName = !this.showDirectorName;
      console.log(this.showDirectorName);
    },
    queryByActorName() {
      this.showActorName = !this.showActorName;
      console.log(this.showActorName);
    },
    queryByName() {
      this.showMainActorName = !this.showMainActorName;
      console.log(this.showMainActorName);
    },
    queryByType() {
      this.showType = !this.showType;
      console.log(this.showType);
    },
    queryByCommenter() {
      this.showCommenter = !this.showCommenter;
      console.log(this.showComment);
    },
    queryByScore() {
      this.showScore = !this.showScore;
      console.log(this.showScore);
    },
    queryByCollaboration() {
      this.showCollaboration = !this.showCollaboration;
      console.log(this.showCollaboration);
    },
    queryBySimilarUser() {
      this.showSimilarUse = !this.showSimilarUse;
      console.log(this.showSimilarUse);
    },
    submit() {
      var idList = [];
      if (this.showTime) {
        idList.push("1");
      } else if (this.showMovieName) {
        idList.push("2");
      } else if (this.showDirectorName) {
        idList.push("3");
      } else if (this.showActorName) {
        idList.push("4");
      } else if (this.showCollaboration) {
        idList.push("5");
      } else if (this.showType) {
        idList.push("6");
      } else if (this.showCommenter || this.showScore) {
        idList.push("7");
      }
      var actors = [this.formItem.actorName];
      if(this.formItem.actorName==null){
        actors=null
      }
      var comment = {};
      comment.id = this.showCommenter ? 1 : 2;
      comment.username = this.commenterName;
      comment.score_from = this.formItem.score_from / 10;
      comment.score_to = this.formItem.score_to / 10;
      if(!this.showCommenter&&!this.showScore){
        comment=null
      }
      var directors = [this.formItem.directorName];
      if(this.formItem.directorName==null){
        directors=null
      }
      var main_actor = this.formItem.mainActorName;
      var movie_name = this.formItem.movieName;
      var time_from = this.formItem.time_from;
      var type = this.formItem.type;
      var version = this.formItem.version;
      var answerType = "data";
      var answer = this.formItem.answer;
      var time = new Date();
      var url="dff"
      if (this.database == "MySql") {
        url = "/MySQL/specify";
      } else if (this.database == "HBase") {
        url = "/HBase/specify";
      } else if (this.database == "Neo4j") {
        url = "/Neo4j/specify";
      }
      let params = {};
      if (this.showCollaboration) {
        params = {
          type: this.formItem.collaborationType,
          threshold: this.formItem.collaborationTime
        };
      } else if (this.showSimilarUser) {
        params = {};
      } else {
        params = {
          idList: idList,
          actors: actors,
          comment: comment,
          directors: directors,
          main_actor: main_actor,
          movie_name: movie_name,
          time_from: time_from,
          time_to: null,
          type: type,
          version: version,
          answerType: answerType,
          answer: answer,
          time: time
        };
      }
      console.log(params);
      axios
        .post(url, params)
        .then(response => {
          var items = [];
          for (var data in response.data) {
            items.push({ result: data });
          }
          this.items = items;
          this.showResult = true;
          this.time = response.time;
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>
